<template>
  <Card v-bind:image="require('../assets/bank.png')">
    <el-autocomplete
      class="inline-input"
      v-model="username"
      :fetch-suggestions="querySearch"
      placeholder="用户名"
      prefix-icon='el-icon-user-solid'
      clearable
    ></el-autocomplete>
    <el-input type="password" placeholder="密码" v-model="password" show-password prefix-icon='el-icon-key' @keyup.enter.native='login' />
    <el-button type="primary" @click="login" :plain="true">登录</el-button>
  </Card>
</template>

<script>
import Card from "../components/Card.vue";

export default {
  name: "Login",
  data() {
    return {
      username: "",
      password: "",
      errMsg: "",
    };
  },
  components: {
    Card,
  },
  methods: {
    login() {
      if (this.username.indexOf('@') > 31 || (!(/^[a-zA-Z]+([-_.][a-zA-Z]+)*[0-9]*@bank\.(com|cn)$/.test(this.username)) && !(/^1[0-9]{10}$/.test(this.username)))) {
        this.$message({
          showClose: true,
          message: '您输入的用户名有误!',
          type: 'error'
        });
        return;
      }
      const article = {
        'username' : this.username,
        'password' : this.encrypt(this.password), 
      };
      const axios = require('axios').default;
      axios.post("/api/login", article)
        .then(response => {
          if (response.data.status === 'bad') {
            this.$message({
              showClose: true,
              message: response.data.errMsg,
              type: 'error'
            });
          } else {
            let loginInfo={
              LoginName:this.username,
              openId:"10086"
            }
            this.cookie.setCookie(loginInfo,7);
            this.$router.push('/balance');
          }
        });
    },
    encrypt(passwd) {
      const PASSWORD_ALPHABET = "0123456789abcdefghijklmnopqrstuvwxyz";
      const CAESAR_KEY = 9;
      let res = "";
      for (let i = 0; i < passwd.length; i ++) {
          let c = passwd[i];
          for (let j = 0; j < PASSWORD_ALPHABET.length; j ++) {
            if (PASSWORD_ALPHABET[j] == c) {
              res += PASSWORD_ALPHABET[(j + PASSWORD_ALPHABET.length - CAESAR_KEY) % PASSWORD_ALPHABET.length];
            }
          }
      }
      return res;
    },
    querySearch(queryString, cb) {
      var list = this.list;
      var results = queryString ? list.filter(this.createFilter(queryString)) : list;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (list) => {
        return (list.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    loadAll() {
      return [
        { "value": "13889401292" },
        { "value": "13478783293" },
        { "value": "admin@bank.cn" },
      ];
    },
    handleSelect(item) {
      console.log(item);
    }
  },
  mounted() {
      this.list = this.loadAll();
  }
};
</script>

<style scoped>
  .el-input--small, .inline-input {
      margin-bottom: 10px;
  }
</style>
